<template>
  <div class="hello">
     <vue-particles
        color="#dedede"  
        :particleOpacity="0.7"
        :particlesNumber="180"
        shapeType="circle"
        :particleSize="3"
        linesColor="#dedede"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="80"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
      >
      </vue-particles>
    <div class="login"  v-loading=loading  @dblclick="getToken()">
      <!-- <el-form :inline="true" label-position="left" :model="loginPost" :rules="rules" ref="loginPost"  class="demo-form-inline">
        <h2>{{title}}</h2>

        <el-form-item label="账号:" prop='LoginUser'>
          <el-input v-model="loginPost.user" placeholder="请输入用户名或者昵称"></el-input>
        </el-form-item>
        <el-form-item label="密码:">
          <el-input type="password" v-model="loginPost.psw" placeholder="请输入密码"></el-input>
        </el-form-item>

         <el-form-item label="确认密码:" prop='checkPsw'>
          <el-input type="password" v-model="reqPost.checkPsw" placeholder="请再次输入密码"></el-input>
        </el-form-item>

        <el-button @click="btnLogin()">登录</el-button>
      </el-form> -->
  
      <el-form :inline="true" label-position="left" label-width="80px"  :model="input" status-icon :rules="rules" ref="input" class="demo-form-inline">
        <h2>{{title}}</h2>
        <el-form-item label="账号:" prop='user'>
          <el-input v-model="input.user" placeholder="请输入用户名或者昵称"></el-input>
        </el-form-item>

        <el-form-item label="密码:" prop='psw'>
          <el-input type="password"  v-model="input.psw" placeholder="请输入密码"></el-input>
        </el-form-item>

        <el-form-item label="确认密码:" prop='checkPsw' v-if = isRegister>
          <el-input type="password" v-model="input.checkPsw" placeholder="请再次输入密码"></el-input>
        </el-form-item>

        <el-button @click="loginOrRegs('input')">{{title}}</el-button>
      </el-form>
  
      
    </div>
   
  </div>
</template>

<script>
import {loginIn,checkUser,registerUser,tast} from '../api/index.js';
import store from '../vuex/store'

export default {
  name: 'login',
  data () {
      var checkUserName =(rule, value, callback)=>{
        if (value === "") {
          callback(new Error('用户名不能为空'));
        }else{
          if(this.isRegister==true){
            checkUser(this.input).then(res=>{
              if(res.code < 0 ){
                callback(new Error('用户名已被占用'));
              }else{
                callback();
              }
            })
          }else{
            callback();
          }
        }

      }
      var pswRule = (rule, value, callback) => {
        if (value === "") {
          callback(new Error('密码不能为空'));
        } 
        else {
          if (this.input.psw !== '' && this.isRegister == true) {
            this.$refs.input.validateField('checkPsw');
          }
          callback();
        }
      };
      var pswRule2 = (rule, value, callback) => {
        if(this.isRegister != true){
          return;
        }
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.input.psw) {
          callback(new Error('两次输入密码不一致!'));
        } else{
          callback();
        }
    };
    return {
      loading:false,
      // isLogin:true,
      isRegister:false,
      title:'登录',
      lgnOrRgst:'',
      input:{
        user:"",
        psw:"",
        checkPsw:""
      },
      rules: {
        user:[{validator:checkUserName,trigger: 'blur'}],
        psw:[{validator: pswRule, trigger: 'blur'}],
        checkPsw:[{validator: pswRule2, trigger: 'change'}]
      }
    }
  },
  mounted () {
    if(this.isRegister == true){
      this.title = "注册"
    }
    
  },
  watch:{

  },
  methods:{
    loginOrRegs(data){
      let that = this;
      if(that.isRegister == false){
        that.btnLogin(data);
      }else if(that.isRegister){
        that.btnReq(data);
      }
    },
    btnLogin(formData){
      let that = this;
      that.$refs[formData].validate((valid) => {
        that.loading = false;
        if (valid) {
          loginIn(that.input).then(res=>{
            if(res.code == 0){
              localStorage.setItem('token',res.token);
              store.state.token = localStorage.getItem('token');
              console.log(res)
              // console.log(store.state.token)
            }else{
              console.log(res.message)
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
      // if(that.input.user == "" || that.input.psw == ""){
      //   return;
      // }
      // loginIn(that.input).then(res=>{
      //   if(res.code == 0){
      //     localStorage.setItem('token',res.token);
      //     store.state.token = localStorage.getItem('token');
      //     console.log(res)
      //     // console.log(store.state.token)
      //   }else{
      //     console.log(res.message)
      //   }
      // })
    },
    btnReq(formData){
      let that = this;
      // that.loading = true;
      that.$refs[formData].validate((valid) => {
        that.loading = false;
        if (valid) {
            that.loading = false;
        } else {
          console.log('error submit!!');
          return false;
        }
      });
  
      // registerUser(that.input).then(res=>{
      //   that.loading = false;
      //   if(res.code == 0){
      //     console.log(res)
      //   }else{
      //     that.$message.error(res.message)
      //   }
      // }).catch( error =>{
      //   that.loading = false;  
      // });
    },
    getToken(){
      let that = this;
      tast().then(res=>{
        // console.log(res)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .hello{
    width: 100%;
    height: 100%;
    background-image: url('../assets/bgColor.jpg');
    position: relative;
    #particles-js{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 99%;
    }
    .login{
      position: absolute;
      background-color: #fff;
      border-radius: 5px;
      width: 355px;
      height: 280px;
      margin: auto;
      top: 0; left: 0; bottom: 0; right: 0;
      z-index: 9999;
      .demo-form-inline{
        // padding: 40px 22px;
        padding-top: 40px;
        padding-left: 37px;
        .el-form-item{
          margin-bottom:20px;
        }
      }
    }
  }
 
 
  
</style>



