<template>
  <div class="hello">
     <vue-particles
        class="canvas"
        color="#dedede"  
        :particleOpacity="0.7"
        :particlesNumber="180"
        shapeType="circle"
        :particleSize="3"
        linesColor="#dedede"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
      >
      </vue-particles>

    <div class="login">
      <h3>注册：</h3><br/>
      用户：<input type="text" value="" v-model="reqPost.user"> <br/>
      密码：<input type="text" value="" v-model="reqPost.psw">  <br/>
      <button @click="btnReq()">注册</button><br/><br/><br/>

      <button @click="getToken()">获取</button>

    </div>
   
  </div>
</template>

<script>
import {registerUser,tast} from '../api/index.js';
import store from '../vuex/store'

export default {
  name: 'login',
  data () {
    return {
      reqPost:{
        user:"",
        psw:""
      }
    }
  },
  mounted () {
    
  },
  watch:{
   
  },
  methods:{
    btnReq(){
      let that = this;
      registerUser(that.reqPost).then(res=>{
        console.log(res)
      })
    },
    getToken(){
      let that = this;
      tast().then(res=>{
        console.log(res)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  *{
    margin: 0;
    padding: 0;
  }
  html,body{
    width: 100%;
    height: 100%;
  }
  .hello{
    width: 100%;
    height: 99%;
  }
  .canvas{
    width: 100%;
    height: 100%;
  }
  .login{
    position: absolute;
    top: 0;
  }
</style>



